<template>
    <div class="layout">
        <div class="layout-sidebar" :style="{ width: sidebarWidth }">
            <sidebar :opacity="opacity" @on-click-menu-icon="onClickMenuIcon" @on-check-weapon="onCheckWeapon"
                @on-check-armor="onCheckArmor" />
        </div>
        <div class="layout-body" :style="{ marginLeft: bodyMarginLeft }">
            <div class="row">
                <div class="col">
                    <h2>{{ gameTitle() }}</h2>
                </div>
                <div class="row">
                    <h3>What monster will you hunt?</h3>
                    <button id="button" @click="randMonster">Check it now!</button>
                    <label id="label-monster">Your target is: <span>{{ monster }}</span></label>
                    <img id="imgMonster" :src="imgMonsterSrc" width="300px" height="190px" />
                </div>
                <div id="weapon-container" class="row" :style="{ height: divWeaponHeight }">
                    <div class="break-line"></div>
                    <h3>What weapon will you use?</h3>
                    <button id="button" @click="randWeapon">Check it now!</button>
                    <label id="label-monster">Your weapon is: <span>{{ weapon }}</span></label>
                    <img id="imgWeapon" :src="imgWeaponSrc" width="180px" height="180px" />
                </div>
                <div id="armor-container" class="row" :style="{ height: divArmorHeight }">
                    <div class="break-line"></div>
                    <h3>What armor will you use?</h3>
                    <button id="button" @click="randArmor">Check it now!</button>
                    <br />
                    <div class="col">
                        <div class="armor-img-container">
                            <img id="imgWeapon" :src="imgArmorSrc.head" width="150px" height="150px" />
                            <label><span>{{ armorName.head }}</span></label>
                        </div>
                        <div class="armor-img-container">
                            <img id="imgWeapon" :src="imgArmorSrc.chest" width="150px" height="150px" />
                            <label><span>{{ armorName.chest }}</span></label>
                        </div>
                        <div class="armor-img-container">
                            <img id="imgWeapon" :src="imgArmorSrc.arm" width="150px" height="150px" />
                            <label><span>{{ armorName.arm }}</span></label>
                        </div>
                        <div class="armor-img-container">
                            <img id="imgWeapon" :src="imgArmorSrc.waist" width="150px" height="150px" />
                            <label><span>{{ armorName.waist }}</span></label>
                        </div>
                        <div class="armor-img-container">
                            <img id="imgWeapon" :src="imgArmorSrc.foot" width="150px" height="150px" />
                            <label><span>{{ armorName.foot }}</span></label>
                        </div>
                    </div>
                </div>
                <br /><br /><br />
            </div>
        </div>
    </div>
</template>

<script lang="ts" setup>
import { ref, onMounted } from 'vue'
import { useRoute } from 'vue-router'
import './style.css'
import { wiMonsters } from '@/util/wi/monsters'
import { wiWeapons } from '@/util/wi/weapons'
import { wiArmors } from '@/util/wi/armors'
import sidebar from '@/components/sidebar/side-bar.vue'

const router = useRoute()

const id = router.params.id
const divWeaponHeight = ref('0px')
const divArmorHeight = ref('0px')
const sidebarWidth = ref('200px')
const bodyMarginLeft = ref('200px')
const opacity = ref('100')
const openAemorRand = ref('false')
const closeMenu = ref('true')
const imgMonsterSrc = ref(require(`@/assets/imgs/${id}/Question.jpg`))
const imgWeaponSrc = ref(require(`@/assets/imgs/question_mark.png`))
const imgArmorSrc = ref({
    head: require(`@/assets/imgs/question_mark.png`),
    chest: require(`@/assets/imgs/question_mark.png`),
    arm: require(`@/assets/imgs/question_mark.png`),
    waist: require(`@/assets/imgs/question_mark.png`),
    foot: require(`@/assets/imgs/question_mark.png`)
})
const armorName = {
    head: '?',
    chest: '?',
    arm: '?',
    waist: '?',
    foot: '?'
}
const monster = ref('?')
const weapon = ref('?')
const gameList = [
    { id: 'wi', name: "World Iceborne" },
    { id: 'rsb', name: "Rise Sunbreak" }
]

const setArmorContainerHieght = (windowWidth: number) => {
    if (windowWidth <= 600) {
        divArmorHeight.value = '1310px'
    } else if (windowWidth <= 725) {
        divArmorHeight.value = '890px'
    } else if (windowWidth <= 1080) {
        divArmorHeight.value = '680px'
    } else {
        divArmorHeight.value = '470px'
    }
}

onMounted(() => {
    if (window.innerWidth <= 600) {
        sidebarWidth.value = '60px'
        bodyMarginLeft.value = '60px'
        opacity.value = '0'
    }

    window.addEventListener('resize', () => {
        const windowWidth = window.innerWidth
        if (windowWidth <= 600) {
            sidebarWidth.value = '60px'
            bodyMarginLeft.value = '60px'
            opacity.value = '0'
        } else {
            if (closeMenu.value != 'true') {
                sidebarWidth.value = '200px'
                bodyMarginLeft.value = '200px'
                opacity.value = '100'
            }
        }
        if (openAemorRand.value == 'true') {
            setArmorContainerHieght(windowWidth)
        }
    })
})

const onClickMenuIcon = () => {
    if (sidebarWidth.value == '200px') {
        sidebarWidth.value = '60px'
        bodyMarginLeft.value = '60px'
        opacity.value = '0'
        closeMenu.value = 'true'
    } else {
        sidebarWidth.value = '200px'
        bodyMarginLeft.value = '200px'
        opacity.value = '100'
        closeMenu.value = 'false'
    }
}

const gameTitle = (): string => {
    const name = gameList.filter(items => items.id === id)[0].name
    return `Monster Hunter ${name}`
}

const onCheckWeapon = (value: string) => {
    weapon.value = '?'
    imgWeaponSrc.value = require(`@/assets/imgs/question_mark.png`)
    if (value == 'false') {
        divWeaponHeight.value = '0px'
    } else {
        divWeaponHeight.value = '470px'
    }
}

const onCheckArmor = (value: string) => {
    const windowWidth = window.innerWidth
    setImgArmorSrc(
        require(`@/assets/imgs/question_mark.png`),
        require(`@/assets/imgs/question_mark.png`),
        require(`@/assets/imgs/question_mark.png`),
        require(`@/assets/imgs/question_mark.png`),
        require(`@/assets/imgs/question_mark.png`)
    )
    setArmorName( '?', '?', '?', '?', '?' )
    if (value == 'false') {
        openAemorRand.value = value
        divArmorHeight.value = '0px'
    } else {
        openAemorRand.value = value
        setArmorContainerHieght(windowWidth)
    }
}

const randMonster = () => {
    let count = 0
    let index = 0
    const interval = setInterval(() => {
        count++
        if (id == "wi") {
            index = Math.floor(Math.random() * 46)
            imgMonsterSrc.value = require(`@/assets/imgs/${id}/monster/${wiMonsters[index]}.jpg`)
            monster.value = wiMonsters[index].replace(/_/g, " ")
        }
        if (count == 30) {
            clearInterval(interval)
        }
    }, 150)
}

const randWeapon = () => {
    let count = 0
    let index = 0
    const interval = setInterval(() => {
        count++
        if (id == 'wi') {
            index = Math.floor(Math.random() * 224)
            imgWeaponSrc.value = require(`@/assets/imgs/${id}/weapon/${wiWeapons[index]}.jpg`)
            weapon.value = wiWeapons[index].replace(/_/g, " ").replace(/\+/g, "/")
        }
        if (count == 30) {
            clearInterval(interval)
        }
    }, 150)
}

const setArmorName = (head: string, chest: string, arm: string, waist: string, foot: string) => {
    armorName.head = head
    armorName.chest = chest
    armorName.arm = arm
    armorName.waist = waist
    armorName.foot = foot
}

const setImgArmorSrc = (head: any, chest: any, arm: any, waist: any, foot: any) => {
    imgArmorSrc.value.head = head
    imgArmorSrc.value.chest = chest
    imgArmorSrc.value.arm = arm
    imgArmorSrc.value.waist = waist
    imgArmorSrc.value.foot = foot
}

const randArmorIndex = (number: number) => {
    let head = Math.floor(Math.random() * number)
    let chest = Math.floor(Math.random() * number)
    let arm = Math.floor(Math.random() * number)
    let waist = Math.floor(Math.random() * number)
    let foot = Math.floor(Math.random() * number)
    return { head: head, chest: chest, arm: arm, waist: waist, foot: foot }
}

const randArmor = () => {
    let count = 0
    const interval = setInterval(() => {
        count++
        let index: any
        if (id == 'wi') {
            index = randArmorIndex(41)
            setImgArmorSrc(
                require(`@/assets/imgs/wi/armor/${wiArmors[index.head]}/head.jpg`),
                require(`@/assets/imgs/wi/armor/${wiArmors[index.chest]}/chest.jpg`),
                require(`@/assets/imgs/wi/armor/${wiArmors[index.arm]}/arm.jpg`),
                require(`@/assets/imgs/wi/armor/${wiArmors[index.waist]}/waist.jpg`),
                require(`@/assets/imgs/wi/armor/${wiArmors[index.foot]}/foot.jpg`)
            )
            setArmorName(
                wiArmors[index.head].replace(/_/g, " "),
                wiArmors[index.chest].replace(/_/g, " "),
                wiArmors[index.arm].replace(/_/g, " "),
                wiArmors[index.waist].replace(/_/g, " "),
                wiArmors[index.foot].replace(/_/g, " ")
            )
        }
        if (count == 30) {
            clearInterval(interval)
        }
    }, 150)
}
</script>