<template>
    <nav>
        <div id="menu-icon" @click="onClickMenuIcon">
            <div class="icon-line"></div>
            <div class="icon-line"></div>
            <div class="icon-line"></div>
        </div>
        <div :style="{width: '170px', transition: '0.5s', opacity: opacity}">
            <RouterLink class="link" to="/">Home</RouterLink>
        </div>
        <div :style="{width: '170px', transition: '0.5s', opacity: opacity}">
            <input type="checkbox" id="checkWeapon" :value="checkWeapon" @change="onCheckWeapon">
            <label for="checkWeapon">Rand weapon</label>
        </div>
        <div :style="{width: '170px', transition: '0.5s', opacity: opacity}">
            <input type="checkbox" id="checkArmor" :value="checkArmor" @change="onCheckArmor">
            <label for="checkArmor">Rand armor</label>
        </div>
    </nav>
</template>

<script lang="ts" setup>
import { ref } from 'vue'

const props = defineProps(['transition', 'opacity'])
const emit = defineEmits(['onCheckWeapon', 'onCheckArmor', 'onClickMenuIcon'])
const checkWeapon = ref('false')
const checkArmor = ref('false')

const onCheckWeapon = () => {
    if (checkWeapon.value == 'false') {
        checkWeapon.value = 'true'
    } else {
        checkWeapon.value = 'false'
    }
    emit('onCheckWeapon', checkWeapon.value)
}

const onCheckArmor = () => {
    if (checkArmor.value == 'false') {
        checkArmor.value = 'true'
    } else {
        checkArmor.value = 'false'
    }
    emit('onCheckArmor', checkArmor.value)
}

const onClickMenuIcon = () => {
    emit('onClickMenuIcon')
}

</script>

<style scoped>
nav {
    padding: 20px 5px 0px 10px;
    display: flex;
    flex-direction: column;
    gap: 20px;
    font-size: 16px;
    color: white;
    font-weight: bold;
}

.link {
    color: white;
    text-decoration: none;
}

#menu-icon {
    background-color: black;
    height: 32px;
    width: 40px;
    display: flex;
    flex-direction: column;
    gap: 6px;
}

.icon-line {
    height: 6px;
    background-color: white;
}
</style>